import React, { useState, useRef, useEffect } from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import io from "socket.io-client";
import axios from "axios";
import pino from "../../../assets/pino.png";
import { styled } from "@mui/material/styles";
import a11L from "../../../assets/a11L.png";
import a11R from "../../../assets/allR.png";

const ButtonCo = styled(Button)(({ theme }) => ({
  color: theme.palette.getContrastText("#beaee2"),
  // lineHeight: "44px",
  borderRadius: "30px",
  fontSize: "20px",
  color: "white !important",
  // padding: "9.5px 16px",
  // height: 40px;
  // padding: 0 14px 0 0;
  // position: "absolute",
  // top: "100%",
  marginTop: "2vh",
  left: "27.5%",
  maxWidth: "45%",
  // background: "linear-gradient(45deg,#FE6B8B,#FF8E53)",
  backgroundColor: "#beaee2",
  "&:hover": {
    // backgroundColor: "#A6095D",
    background: "linear-gradient(45deg,#FE6B8B,#FF8E53)",
  },
}));

const ButtonCo2 = styled(Button)(({ theme }) => ({
  color: theme.palette.getContrastText("#beaee2"),
  // lineHeight: "44px",
  borderRadius: "30px",
  fontSize: "20px",
  color: "white !important",
  // padding: "9.5px 16px",
  // height: 40px;
  // padding: 0 14px 0 0;
  // position: "absolute",
  // top: "100%",
  marginTop: "2vh",
  left: "27.5%",
  maxWidth: "45%",
  // background: "linear-gradient(45deg,#FE6B8B,#FF8E53)",
  backgroundColor: "#beaee2",
  "&:hover": {
    // backgroundColor: "#A6095D",
    background: "linear-gradient(45deg,#FE6B8B,#FF8E53)",
  },
}));

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
  borderRadius: "10px",
};
const socket = io.connect("https://i7a402.p.ssafy.io:4000");
function GameIntro(props) {
  //variables
  const role = props.role;
  const [open, setOpen] = useState(props.open);
  const [open2, setOpen2] = useState(false);
  const [gameReady, setGameReady] = useState(0);
  const [disabled, setDisabled] = useState(false);
  //function
  const onClick = () => {
    setOpen(false);
    setOpen2(true);
    socket.emit("gameReadyCount");
  };
  socket.on("gameReadyCount", (cnt) => {
    setGameReady(cnt.gameReady);
  });
  socket.on(
    "leaveSession",
    (
      count,
      firstCount,
      gameReady,
      answerCount,
      discussCount,
      finalCount,
      gameSetCount
    ) => {
      setGameReady(0);
    }
  );

  const onClick2 = () => {
    socket.emit("gameReady");
    setDisabled(true);
  };
  socket.on("gameReady", (cnt) => {
    setGameReady(cnt.gameReady);
    if (cnt.gameReady === props.participantNum) {
      setOpen2(false);
      props.setGameStart();
    }
  });

  return (
    <div>
      <Modal
        open={open}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box
          style={{
            borderRadius: "3vw",
            border: "4px solid #f7dbf0",
            backgroundColor: "#f7dbf0",
          }}
          sx={style}
        >
          <img
            alt="pino"
            src={pino}
            style={{
              position: "absolute",
              width: "50px",
              left: "7%",
            }}
          />
          <img
            alt="pino"
            src={pino}
            style={{
              position: "absolute",
              width: "50px",
              left: "83%",
            }}
          />
          <Typography
            style={{ textAlign: "center", fontSize: "30px" }}
            id="modal-modal-title"
            variant="h6"
            component="h2"
          >
            MBTI !
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            {/* <p > </p>   !  */}
            <div style={{ textAlign: "center", fontSize: "20px" }}>
               <span style={{ color: "red" }}></span>{" "}
              <span style={{ color: "blue" }}> </span>  
                .
              <br />
              <span style={{ color: "red" }}></span>  3{" "}
              <span style={{ color: "red" }}></span>  
                 .  {" "}
              <span style={{ fontWeight: "bold" }}>4</span>   
               1   . <br />
              4    {" "}
              <span style={{ color: "red" }}></span> 
              .
              <br />
              <span style={{ color: "red" }}></span> 1 
              ,   <span style={{ color: "red" }}>+1</span>,
                <span style={{ color: "blue" }}>-1</span> 
              .
              <br /> {" "}
              <span style={{ color: "green", fontWeight: "bold" }}>
                  
              </span>
                !     {" "}
              <span style={{ color: "red" }}></span> 
              !
            </div>
          </Typography>
          <ButtonCo2 fullWidth variant="contained" onClick={onClick}>
             
          </ButtonCo2>
        </Box>
      </Modal>
      <Modal
        open={open2}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box
          style={{
            borderRadius: "3vw",
            border: "4px solid #f7dbf0",
            backgroundColor: "#f7dbf0",
          }}
          sx={style}
        >
          <img
            alt="a11L"
            src={a11L}
            style={{ position: "absolute", width: "50px", left: "7%" }}
          />

          <img
            alt="a11R"
            src={a11R}
            style={{ position: "absolute", width: "50px", left: "83%" }}
          />
          <Typography
            style={{ textAlign: "center", fontSize: "30px" }}
            id="modal-modal-title"
            variant="h6"
            component="h2"
          >
             ?
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            {role === "mafia" ? (
              <div style={{ textAlign: "center", fontSize: "20px" }}>
                 <span style={{ color: "red" }}></span>!
                     !{" "}
              </div>
            ) : (
              <div style={{ textAlign: "center", fontSize: "20px" }}>
                 <span style={{ color: "blue" }}> </span>!
                         
                !{" "}
              </div>
            )}
          </Typography>
          <ButtonCo
            fullWidth
            variant="contained"
            onClick={onClick2}
            disabled={disabled}
          >
             {gameReady}/{props.participantNum}
          </ButtonCo>
        </Box>
      </Modal>
    </div>
  );
}

export default React.memo(GameIntro);
