import React, { useState, useEffect, useRef } from "react";
import io from "socket.io-client";
import { motion } from "framer-motion";
const padNumber = (num, length) => {
  return String(num).padStart(length, "0");
};
const socket = io.connect("https://i7a402.p.ssafy.io:4000");
const Timer = (props) => {
  //    undefined    ..
  const tempMin = props.min ? parseInt(props.min) : 0;
  const tempSec = props.sec ? parseInt(props.sec) : 0;
  //    initialTime setInterval  interval ref
  const initialTime = useRef(tempMin * 60 + tempSec);
  const interval = useRef(null);

  const [sec, setSec] = useState(padNumber(tempSec, 2));
  const [min, setMin] = useState(padNumber(tempMin, 2));
  const [discussCount, setDiscussCount] = useState(0);
  const [countSound] = useState(
    new Audio(require("./mixkit-tick-tock-clock-timer-1045.wav"))
  );
  const [red, setRed] = useState(false);
  useEffect(() => {
    interval.current = setInterval(() => {
      initialTime.current -= 1;
      setMin(padNumber(parseInt(initialTime.current / 60), 2));
      setSec(padNumber(initialTime.current % 60, 2));
    }, 1000);
    return () => clearInterval(interval.current);
  }, []);
  //     
  useEffect(() => {
    socket.emit("discussCount");
  }, []);
  socket.on("discussCount", (cnt) => {
    setDiscussCount(cnt.discussCount);
  });
  socket.on(
    "leaveSession",
    (
      count,
      firstCount,
      gameReady,
      answerCount,
      discussCount,
      finalCount,
      gameSetCount
    ) => {
      setDiscussCount(0);
    }
  );

  //     useEffect
  // initialTime  0  interval .
  useEffect(() => {
    if (initialTime.current <= 0) {
      clearInterval(interval.current);

      if (discussCount === props.participantNum * 2) {
        props.setMode(6);
        console.log("go to 6");
      } else {
        props.setMode(5);
        console.log("go to 5");
      }
    }
    if (initialTime.current <= 10) {
      setRed(true);
      countSound.volume = 0.5;
      countSound.play();
      if (initialTime.current <= 0) {
        countSound.pause();
      }
    }
  }, [sec]);

  return (
    <div style={{ position: "absolute", top: "94%", left: "48.5%" }}>
      {!red ? (
        <div className={red ? "timeout" : null}>
          {min} : {sec}
        </div>
      ) : (
        <div>
          <motion.div
            animate={{
              scale: [1, 2.5, 1],
              originX: 0,
              color: "#c71100",
            }}
            transition={{
              ease: "easeInOut",
              duration: 1,
              repeat: 10,
            }}
          >
            {sec}
          </motion.div>
        </div>
      )}
    </div>
  );
};

export default Timer;
