import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { loginUser } from "../../_actions/user_action";
import { useParams, useLocation, useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";

import { alpha, styled } from "@mui/material/styles";
import logo_ani from "../../assets/logo_ani.gif";
import MemberPage from "../Common/MemberPage";
import Footer from "../Common/Footer";

import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import Register from "../RegisterPage/RegisterPage";
// css   

//  24px

//       #141414v
//   #F3F3F3
//   #686868

const ButtonCo = styled(Button)(({ theme }) => ({
  color: theme.palette.getContrastText("#beaee2"),
  lineHeight: "44px",
  borderRadius: "30px",
  fontSize: "22px",
  padding: "9.5px 16px",
  // background: "linear-gradient(45deg,#FE6B8B,#FF8E53)",
  backgroundColor: "#beaee2",
  "&:hover": {
    // backgroundColor: "#A6095D",
    background: "linear-gradient(45deg,#FE6B8B,#FF8E53)",
  },
}));

// style={{ margin: "auto 5px auto 0px !important" }}

const Grid1 = styled(Grid)({
  "& .MuiGrid-root": {
    margin: "auto 10px auto 0px ",
  },
});

const TextFieldLogin = styled(TextField)({
  "& .MuiFormLabel-root": {
    color: "#141414", //  
    left: "0.5%",
    top: "7%",
    // textAlign: "center !important",
  },
  "& label.Mui-focused": {
    color: "#ffffff", //  
  },
  "& .MuiInput-underline:after": {
    borderBottomColor: "white",
  },
  "& .MuiOutlinedInput-root": {
    "& input": {
      //   
      color: "#141414",
      fontSize: "21px",
    },
    "& fieldset": {
      borderColor: "white",
      borderRadius: "30px",
    },
    "&:hover fieldset": {
      borderColor: "white",
    },
    "&.Mui-focused fieldset": {
      borderColor: "white",
    },
  },
});

function LoginPage(props) {
  const params = useParams();
  const location = useLocation();
  const navigate = useNavigate();

  const dispatch = useDispatch();

  const [Email, setEmail] = useState("");
  const [Password, setPassword] = useState("");

  const onEmailHandler = (event) => {
    setEmail(event.currentTarget.value);
  };

  const onPasswordHandler = (event) => {
    setPassword(event.currentTarget.value);
  };
  const onClick = () => {
    navigate("/Register");
  };

  const onSubmitHandler = (event) => {
    event.preventDefault();

    let body = {
      id: Email,
      password: Password,
    };

    dispatch(loginUser(body))
      .then((response) => {
        console.log(response.payload.data.accessToken);
        localStorage.setItem("token", response.payload.data.accessToken);
        navigate("/");
      })
      .catch((error) => {
        console.log(error);
        let error_code = error.response.data.message;
        if (error_code === "Invalid Id") {
          alert(" ");
          window.location.replace("/login");
        } else if (error_code === "Invalid Password") {
          alert(" ");
          // window.location.replace("/login");
        } else {
          alert("   ");
        }
        console.log(error.response.data.message);
      });
  };

  return (
    <div>
      <MemberPage></MemberPage>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 5,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
          style={{
            justifyContent: "center",
            height: "100vh",
          }}
        >
          <div
            style={{
              // width: "40vw",
              maxWidth: "175px",
              // height: "40vw",
              maxHeight: "175px",
              overflow: "hidden",
              borderRadius: "50%",
            }}
          >
            <img
              alt="logo_ani"
              src={logo_ani}
              style={{
                maxWidth: "350px",
                width: "350px",
                marginLeft: "-50%",
                marginTop: "-10%",
              }}
            />
          </div>

          <form
            style={{ display: "flex", flexDirection: "column" }}
            onSubmit={onSubmitHandler}
          >
            <Box
              component="form"
              // onSubmit={handleSubmit}
              noValidate
              sx={{ mt: 1 }}
            >
              <TextFieldLogin
                margin="normal"
                fullWidth
                value={Email}
                onChange={onEmailHandler}
                id="email"
                label=""
                name="email"
                type="email"
                autoComplete="email"
                autoFocus
                // sx={{ mx: "auto" }}
              />
              <TextFieldLogin
                margin="normal"
                fullWidth
                value={Password}
                onChange={onPasswordHandler}
                id="password"
                name="password"
                label=""
                type="password"
                autoComplete="current-password"
              />

              <ButtonCo
                onClick={onSubmitHandler}
                fullWidth
                variant="contained"
                sx={{ mt: 2, mb: 3 }}
              >
                <b></b>
              </ButtonCo>
            </Box>
          </form>
          <Typography align="center">
            <Link
              onClick={onClick}
              underline="hover"
              variant="h6"
              color="inherit"
            >
               ? <b> </b>
            </Link>
            <br></br>
            {/*    */}
            {/* <Link
              href="#"
              underline="hover"
              variant="subtitle2"
              color="inherit"
            >
                 
            </Link> */}
          </Typography>
        </Box>
      </Container>
      <Footer sx={{ mt: 5, mb: 3 }} />;
    </div>
  );
}

export default LoginPage;
